import styles from "./css/selectPeopleModal.module.css";
import React, { useState, useRef, useEffect } from "react";
import { motion } from "framer-motion";

const App = (props) => {
    const delay = ms => new Promise(
        resolve => setTimeout(resolve, ms)
    );

    const [modalAreaBottom, setModalAreaBottom] = useState('-50');

    const nextPage = async event => {
        await delay(1);
        setModalAreaBottom('0');
    }

    const targetRef = useRef(null);  
    const handleScroll = () => {
        console.log("scrolling");
        
        if (window.scrollY > 0) {
        targetRef.current.style.position = "fixed";      
        }
    };

    useEffect(() => {    
        const timer = setInterval(() => {
        window.addEventListener("scroll", handleScroll);
        }, 100);
        return () => {
        clearInterval(timer);
        window.removeEventListener("scroll", handleScroll);
        };
    }, []);

    const onCancel = () => {
        props.modalState();
    }

    return(
        <div className={styles.Modal}>
            <div className={styles.ModalBackground}></div>
            <div className={styles.ModalArea} style={{bottom:modalAreaBottom}}>
            </div>
        </div>
    )
}

export default App;
